import { useState } from "react";
import Buttons from "./Buttons";
import Search from "./Search";
import Table from "./Table";

const Main = () => {
	const [filterControl, setFilterControl] = useState("все");
	const [filterSmp, setFilterSmp] = useState("все");
	
	return (
		<div className="main">
			<Buttons />
			<Search
				setFilterControl={setFilterControl}
				setFilterSmp={setFilterSmp}
			/>
			<Table
				filterControl={filterControl}
				filterSmp={filterSmp}
				setFilterControl={setFilterControl}
				setFilterSmp={setFilterSmp}
			/>
		</div>
	);
};

export default Main;
