import { useState, useEffect } from "react";
import { path } from "./helpers/path.js";

const FormControlSelector = (props) => {
	const [control, setControl] = useState(null);
	const [controlValue, setControlValue] = useState(null);
	const { getCurrentControl } = props;

	getCurrentControl(controlValue);

	useEffect(() => {
		fetch(path + "/control/")
			.then((res) => res.json())
			.then((data) => setControl(data));
	}, []);

	console.log(controlValue);
	return (
		<div className="control_block d-flex justify-content-between align-items-center mb-5">
			<label htmlFor="selectorControl ">Контр. орган</label>
			<select
				name="control"
				id="control"
				onChange={(e) => setControlValue(e.target.value)}>
				{control &&
					control.map((elem) => {
						return (
							<option key={elem.id} value={elem.translate}>
								{elem.translate}
							</option>
						);
					})}
			</select>
		</div>
	);
};

export default FormControlSelector;
