import { Link } from "react-router-dom";

const Menu = (props) => {
	return (
		<div className="menu">
			<ul className="main_menu">
				<li className="menu_item">
					<Link to="/">Главная</Link>
				</li>
				<li className="menu_item">
					<Link to="/spravka">О программе</Link>
				</li>
			</ul>
		</div>
	);
};

export default Menu;
