import { useState, useEffect } from "react";

const ControlSelector = (props) => {
	const [controlOrgs, setControlOrgs] = useState(null);
	const { setFilterControl } = props;


	useEffect(() => {
		fetch("https://smooth-earthy-minibus.glitch.me/control/")
			.then((res) => res.json())
			.then((data) => setControlOrgs(data));
	}, []);

	return (
		<div className="control_block d-flex justify-content-between align-items-center mb-5">
			<label htmlFor="selectorControl ">Контр. орган</label>
			<select
				name="control"
				id="control"
				onChange={(e) => setFilterControl(e.target.value.toLowerCase())}>
				{controlOrgs &&
					controlOrgs.map((elem) => {
						return (
							<option key={elem.id} value={elem.translate}>
								{elem.translate}
							</option>
						);
					})}
			</select>
		</div>
	);
};

export default ControlSelector;
