import { useState, useEffect } from "react";
import { path } from "./helpers/path.js";
import { Link } from "react-router-dom";
import React from "react";
import axios from "axios";
import ExportExcel from "./ExportExcel.jsx";

const Table = (props) => {
	const [reestr, setReestr] = useState(null);
	const { filterControl, filterSmp } = props;
	const [triggerDel, setTriggerDel] = useState(null);
	const fileName = "Реестр проверок";

	

	useEffect(() => {
		fetch(path + "/reestr/")
			.then((res) => res.json())
			.then((data) => setReestr(data))
	}, [triggerDel]);

	let filteredTable = [];

	if (filterControl === "все") {
		filteredTable = reestr;
	} else if (filterControl !== "все") {
		filteredTable =
			reestr &&
			reestr.filter((elem) => {
				return (
					elem.control.toLowerCase() === filterControl.toLowerCase()
				);
			});
	}

	if (filterSmp !== "все") {
		filteredTable =
			filteredTable &&
			filteredTable.filter((elem) => {
				return elem.smp.toLowerCase() === filterSmp.toLowerCase();
			});
	}

	const deleteRow = (e) => {
		const ID = e.target.parentNode.parentNode.dataset.id;
		fetch(path + "/reestr/" + ID, { method: "DELETE" })
			.then(alert("Запись реестра удалена"))
			.then(setTriggerDel("yes"));
	};

	const getID = (e) => {
		const ID = e.target.parentNode.parentNode.parentNode.dataset.id;
		localStorage.setItem("id", JSON.stringify(ID));
	};

	return (
		<div className="table pl-4 pr-4">
			<h4 className="text-center">Реестр проверок</h4>
			<table className="table table-hover table-success text-center table-responsive-lg">
				<thead>
					<tr>
						<th>№ п/п</th>
						<th>Проверяемый СМП</th>
						<th>Контролирующий орган</th>
						<th>Плановый период проверки</th>
						<th className="border-right">Плановая длительность</th>
						<th>Удаление</th>
						<th>Редактирование</th>
					</tr>
				</thead>
				<tbody>
					{reestr &&
						filteredTable.map((elem) => {
							return (
								<tr
									key={elem.id}
									data-id={elem.id}
									className="reestr_row">
									<td>{elem.id}</td>
									<td>{elem.smp}</td>
									<td>{elem.control}</td>
									<td>
										{elem.dateFrom}-{elem.dateTo}
									</td>
									<td className="border-right">
										{elem.planDays}
									</td>
									<td>
										<button
											className="btn btn-danger delBtn"
											onClick={deleteRow}>
											Удалить
										</button>
									</td>
									<td>
										<button className="btn btn-warning editBtn">
											<Link
												onClick={getID}
												to={`/editForm`}>
												Редактировать
											</Link>
										</button>
									</td>
								</tr>
							);
						})}
				</tbody>
			</table>
		</div>
	);
};

export default Table;
