import ControlSelector from "./ControlSelector";
import SmpSelector from "./SmpSelector";

const Search = (props) => {
	const { filterControl, setFilterControl, filterSmp, setFilterSmp } = props;

	return (
		<div className="search mb-3 mt-5 border p-3 pr-4">
			<h4 className="text-center">Фильтр</h4>
			<div className="selectors_block">
				<div className="control_block">
					<ControlSelector setFilterControl={setFilterControl} filterControl={filterControl} />
				</div>
				<div className="smp_block">
					<SmpSelector filterSmp={filterSmp} setFilterSmp={setFilterSmp}/>
				</div>
			</div>
		</div>
	);
};

export default Search;
