import bird from "./../img/bird.png";
import { path } from "./helpers/path.js";
import FormSmpSelector from "./FormSmpSelector";
import FormControlSelector from "./FormControlSelector";
import { useState } from "react";

const AddEditForm = () => {
	const [smpValue, setSmpValue] = useState(null);
	const [controlValue, setControlValue] = useState(null);

	const saveSmp = () => {
		const smpData = {
			name: document.querySelector("#smp_name").value,
			translate: document.querySelector("#smp_name").value,
		};

		fetch(path + "/smp/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(smpData),
		}).then(alert("Субъект добавлен"));
	};

	const saveControl = () => {
		const controlData = {
			name: document.querySelector("#control_name").value,
			translate: document.querySelector("#control_name").value,
		};

		fetch(path + "/control/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(controlData),
		}).then(alert("Контролирующий орган добавлен"));
	};

	const getCurrentSmp = (smp) => {
		setSmpValue(smp);
	};

	const getCurrentControl = (control) => {
		setControlValue(control);
	};

	const saveData = (e) => {
		e.preventDefault();
		const newData = {
			smp: smpValue,
			control: controlValue,
			dateFrom: document.querySelector("#dateFrom").value,
			dateTo: document.querySelector("#dateTo").value,
			planDays: document.querySelector("#plan").value,
		};

		fetch(path + "/reestr/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(newData),
		})
			.then(alert("Проверка добавлена в реестр"))
			.then((window.location.href = "/"));
	};

	return (
		<div className="form">
			<div className="form_holder border rounded shadow p-4">
				<h2 className="form_heading">Добавление проверки</h2>
				<form className="add_edit_form">
					<div className="smp_block mb-4 d-flex justify-content-between">
						<FormSmpSelector getCurrentSmp={getCurrentSmp} />
						<input
							type="text"
							id="smp_name"
							className="smp mr-4 ml-4 pl-2"
							placeholder="Добавить субъект в базу"
						/>
						<button
							className="btn btn-success add_btn_form p-0  m-0 d-flex align-items-center justify-content-center"
							onClick={saveSmp}>
							<img src={bird} alt="..." className="bird" />
						</button>
					</div>

					<div className="dates d-flex justify-content-between align-items-center mb-5">
						<label htmlFor="date_from" className="mr-3">
							Период проверки с
						</label>
						<input
							type="date"
							id="dateFrom"
							className="pl-2 pr-2"
						/>
						<label htmlFor="date_to">по</label>
						<input type="date" id="dateTo" className="pl-2" />
					</div>
					<div className="control_block mb-4 d-flex justify-content-between">
						<FormControlSelector
							getCurrentControl={getCurrentControl}
						/>
						<input
							type="text"
							id="control_name"
							className="control mr-4 ml-4 pl-2"
							placeholder="Добавить контр.орган в базу"
						/>
						<button
							className="btn btn-success add_btn_form p-0  m-0 d-flex align-items-center justify-content-center"
							onClick={saveControl}>
							<img src={bird} alt="..." className="bird" />
						</button>
					</div>
					<div className="plan_block d-flex justify-content-between align-items-center">
						<label htmlFor="plan">
							Плановая длительность проверки (дней)
						</label>
						<input type="text" id="plan" />
					</div>
					<button
						type="submit"
						className="btn btn-primary "
						id="FormSaveBtn"
						onClick={saveData}>
						Сохранить
					</button>
				</form>
			</div>
		</div>
	);
};

export default AddEditForm;
