import { Link } from "react-router-dom";
import ExportExcel from "./ExportExcel";

const Buttons = (props) => {
	return (
		<div className="buttons pr-4 pl-4">
			<button className="btn btn-secondary add_btn mr-1 shadow">
				<Link to={"/form"}>Добавить проверку</Link>
			</button>
			<ExportExcel/>
		</div>
	);
};

export default Buttons;
