import { useState, useEffect } from "react";
import { path } from "./helpers/path";

const EditForm = (props) => {
	const [currentControl, setCurrentControl] = useState(null);
	const currentID = JSON.parse(localStorage.getItem("id"));

	useEffect(() => {
		fetch(path + "/reestr/" + currentID)
			.then((res) => res.json())
			.then((data) => setCurrentControl(data));
	}, [currentID]);

	const saveNewData = (e) => {
		e.preventDefault();
		const newData = {
			smp: document.querySelector("#smp_name").value,
			dateFrom: document.querySelector("#dateFrom").value,
			dateTo: document.querySelector("#dateTo").value,
			control: document.querySelector("#control_name").value,
			planDays: document.querySelector("#plan").value,
		};
		fetch(path + "/reestr/" + currentID, {
			method: "PUT",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify(newData),
		})
			.then(alert("Данные успешно изменены"))
			.then((window.location.href = "/"));
	};

	return (
		<div className="form">
			<div className="form_holder border rounded shadow p-4">
				<h2 className="form_heading">Редактировать запись реестра</h2>
				<form className="add_edit_form">
					<div className="smp_block mb-4 d-flex justify-content-between">
						<label htmlFor="smp_name">Субъект</label>
						<input
							type="text"
							id="smp_name"
							className="smp"
							defaultValue={currentControl && currentControl.smp}
						/>
					</div>

					<div className="dates d-flex justify-content-between align-items-center mb-5">
						<label htmlFor="date_from" className="mr-3">
							Период проверки с
						</label>
						<input
							type="date"
							id="dateFrom"
							className="pl-2 pr-2"
							defaultValue={
								currentControl && currentControl.dateFrom
							}
						/>
						<label htmlFor="date_to">по</label>
						<input
							type="date"
							id="dateTo"
							className="pl-2"
							defaultValue={
								currentControl && currentControl.dateTo
							}
						/>
					</div>
					<div className="control_block mb-4 d-flex justify-content-between">
						<label htmlFor="control_name">
							Контролирующий орган
						</label>
						<input
							type="text"
							id="control_name"
							className="control"
							defaultValue={
								currentControl && currentControl.control
							}
						/>
					</div>

					<div className="plan_block d-flex justify-content-between align-items-center">
						<label htmlFor="plan">
							Плановая длительность проверки
						</label>
						<input
							type="text"
							id="plan"
							defaultValue={
								currentControl && currentControl.planDays
							}
						/>
					</div>
					<button
						type="submit"
						className="btn btn-primary "
						id="FormSaveBtn"
						onClick={saveNewData}>
						Сохранить
					</button>
				</form>
			</div>
		</div>
	);
};
export default EditForm;
