import { useState, useEffect } from "react";
import { path } from "./helpers/path.js";

const FormSmpSelector = (props) => {
	const [smp, setSmp] = useState(null);
	const [smpValue, setSmpValue] = useState(null);
	const { getCurrentSmp } = props;

	getCurrentSmp(smpValue);

	useEffect(() => {
		fetch(path + "/smp/")
			.then((res) => res.json())
			.then((data) => setSmp(data));
	}, []);

	console.log(smpValue);

	return (
		<div className="smp_block d-flex align-items-center mb-5">
			<label htmlFor="spm ">Субъект</label>
			<select
				name="smp"
				id="smp"
				onChange={(e) => setSmpValue(e.target.value)}>
				{smp &&
					smp.map((elem) => {
						return (
							<option key={elem.id} value={elem.translate}>
								{elem.translate}
							</option>
						);
					})}
			</select>
		</div>
	);
};

export default FormSmpSelector;
