import { useState, useEffect } from "react";
import { path } from "./helpers/path";

const SmpSelector = (props) => {
	const [smp, setSmp] = useState(null);
	const { setFilterSmp } = props;



	useEffect(() => {
		fetch(path + "/smp/")
			.then((res) => res.json())
			.then((data) => setSmp(data));
	}, []);

	return (
		<div className="smp_block d-flex align-items-center mb-5">
			<label htmlFor="spm ">Субъект</label>
			<select
				name="smp"
				id="smp"
				onChange={(e) => setFilterSmp(e.target.value.toLowerCase())}>
				{smp &&
					smp.map((elem) => {
						return (
							<option key={elem.id} value={elem.translate}>
								{elem.translate}
							</option>
						);
					})}
			</select>
		</div>
	);
};

export default SmpSelector;
